.wrapper {
  overflow: auto;
  overflow-scrolling: touch;
  -webkit-overflow-scrolling: touch;
  -webkit-mask-image: linear-gradient(90deg,transparent,#000 1em,#000 calc(100% - 1em),transparent);
}

.wrapper::-webkit-scrollbar {
  width: 0;
  height: 0;
  background: transparent;
}

.items {
  display: flex;
}

.items:after,
.items:before {
  content: "";
  flex-grow: 1;
  min-width: 1em;
}

.item {
  padding: 1.25em 1em;
}

.in {
  position: relative;
  border: none;
  outline: none;
  white-space: nowrap;
  color: inherit;
  font-size: inherit;
  height: 2.5em;
  padding: .5em 1em;
  font-weight: 500;
  background: #fff;
  overflow: hidden;
  border-radius: 1.25em;
  box-shadow: 0 .25em .2em rgba(0,0,0,.1);
}

.in:after,
.in:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  border-radius: inherit;
}

.in:before {
  border: 1px solid;
}

.in:after {
  background-color: currentColor;
  transition-property: visibility,-webkit-transform;
  transition-property: transform,visibility;
  transition-property: transform,visibility,-webkit-transform;
  transition-duration: .2s;
  -webkit-transform: scale(0);
  transform: scale(0);
  visibility: hidden;
}

.isActive .in:after {
  -webkit-transform: none;
  transform: none;
  visibility: visible;
}

.viewShort .in {
  padding: 0;
  width: 2.5em;
}

.text {
  position: relative;
  z-index: 1;
  color: #5f5f5f;
  transition: color .2s;
  font-weight: 600;
}

.isActive .text {
  color: #fff;
}




