.wrapper {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  overflow: auto;
  padding: 2em 1em 1em;
  margin: 0 -1em;
}

.list {
  margin-bottom: 3em;
}

.listItem {
  position: relative;
  padding-right: 2em;
}

.listItem+.listItem {
  margin-top: 1.5em;
}

.listTitle {
  font-size: 1.25em;
  font-weight: 600;
}

.listNote {
  margin: .25em 0 0;
}

.listArrow {
  width: 2em;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  opacity: .3;
}

.listAction,
.listArrow {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
}

.listAction {
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
}

.other {
  display: flex;
}

.other:before {
  content: "";
  flex-grow: 1;
}

.logout {
  border: none;
  outline: none;
  padding: 0;
  background: none;
  font-size: inherit;
  order: -1;
}

.social {
  color: inherit;
  text-decoration: none;
  font-size: 2em;
  opacity: .9;
}

.social+.social {
  margin-left: .5em;
}

.popups {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  pointer-events: none;
  z-index: 1;
}

.version {
  flex-grow: 1;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  padding-top: 1em;
}




