@-webkit-keyframes a {
  0% {
    opacity: 0;
  }
}

@keyframes a {
  0% {
    opacity: 0;
  }
}

.wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  -webkit-animation: a .5s;
  animation: a .5s;
}

.wrapper,
.wrapper>:not(button) {
  flex-grow: 1;
}

.wrapper input {
  border: none;
  border-bottom: 1px solid;
  padding: .5em 0;
  font-size: 1.2em;
  outline: none;
}

.wrapper button {
  border: none;
  outline: none;
  background: none;
  font-size: inherit;
  padding: 1em;
}

.wrapper button i {
  margin-left: .5em;
}




