@import '../../common';

$transition-duration: .2s;

.wrapper {
	@include horizontal-scroll; }

.items {
	display: flex;

	&::before,
	&::after {
		content: '';
		flex-grow: 1;
		min-width: 1em; } }

.item {
	padding: 1.25em 1em; }

.in {
	$height: 2.5em;

	position: relative;
	border: none;
	outline: none;
	white-space: nowrap;
	color: inherit;
	font-size: inherit;
	height: $height;
	padding: .5em 1em;
	font-weight: 500;
	background: #FFFFFF;
	overflow: hidden;
	border-radius: ($height / 2);
	box-shadow: 0 .25em .2em rgba(#000000, .1);

	&::before,
	&::after {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		bottom: 0;
		right: 0;
		border-radius: inherit; }

	&::before {
		border: 1px solid; }

	&::after {
		background-color: currentColor;
		transition-property: transform, visibility;
		transition-duration: $transition-duration;
		transform: scale(0);
		visibility: hidden;

		.isActive & {
			transform: none;
			visibility: visible; } }

	.viewShort & {
		padding: 0;
		width: $height; } }

.text {
	position: relative;
	z-index: 1;
	color: #5F5F5F;
	transition: color $transition-duration;
	font-weight: $fw-bold;

	.isActive & {
		color: #FFFFFF; } }



