@import '../../common';

.wrapper {
	margin: 0 -1em;
	flex-grow: 1;
	position: relative;
	--product-width: 100%;

	@media (min-aspect-ratio: 9/10) {
		--product-width: 80%; }

	@media (min-aspect-ratio: 10/10) {
		--product-width: 50%; }

	@media (min-aspect-ratio: 12/10) {
		--product-width: #{(100% / 3)}; }

	@media (min-aspect-ratio: 14/10) {
		--product-width: 25%; }

	@media (min-aspect-ratio: 18/10) {
		--product-width: 20%; }

	@media (min-aspect-ratio: 26/10) {
		--product-width: #{(100% / 7)}; }

	@media (min-aspect-ratio: 34/10) {
		--product-width: #{(100% / 9)}; }

	@media (min-aspect-ratio: 40/10) {
		--product-width: #{(100% / 11)}; }

	@media (min-aspect-ratio: 50/10) {
		--product-width: #{(100% / 13)}; } }

.counter {
	@include horizontal-scroll;
	padding-top: 1em;
	height: 100%;

	&In {
		display: flex;
		height: inherit;

		&::before,
		&::after {
			content: '';
			width: calc((100% - var(--product-width)) / 2);
			display: block;
			flex-shrink: 0; } } }

.product {
	width: var(--product-width);
	flex-shrink: 0;
	display: flex; }

.background {
	position: absolute;
	bottom: -4em;
	left: 0;
	right: 0;
	height: $product-info-min-height + 4em;
	background-color: #F6F6F6; }

.labels {
	position: absolute;
	left: 0;
	right: 0;
	bottom: $product-info-min-height - 2em; }



