@import '../../common';

.wrapper {
	flex-grow: 1;
	overflow: auto;
	margin: 0 -1em -1em;
	padding: 0 1em 1em; }

.list {
 }	//

.item {
	position: relative;
	border: 1px solid #CCCCCC;
	padding: .5em;
	border-radius: .2em;
	box-shadow: 0 2px 3px rgba(#000000, .05);
	transition-property: color, border-color;
	transition-duration: .1s;

	&.viewActive {
		border-color: $brand-color;
		color: $brand-color; }

	& + & {
		margin-top: 1em; } }

.title {
	margin: 0;
	font-size: 1.15em;
	font-weight: $fw-bold; }

.address {
	margin: .5em 0 0; }

.actions {
	display: flex;
	margin-top: .5em; }

.action {
	position: relative;
	z-index: 1;
	outline: none;
	border: none;
	background: none;
	font-size: 1.2em;
	padding: 0;
	color: $brand-color;

	& + & {
		margin-left: .75em; } }

.setDefault {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	opacity: 0; }

.add {
	margin-top: 1em;
	width: 100%;
	border: none;
	background: none;
	outline: none;
	padding: 1em;
	font-size: inherit;
	font-weight: $fw-bold; }



