@-webkit-keyframes a {
  0% {
    opacity: 0;
  }

  40% {
    opacity: 1;
  }

  50% {
    opacity: 1;
  }

  to {
    -webkit-transform: translate(-40vw,100vw) scale(.1);
    transform: translate(-40vw,100vw) scale(.1);
    opacity: 0;
  }
}

@keyframes a {
  0% {
    opacity: 0;
  }

  40% {
    opacity: 1;
  }

  50% {
    opacity: 1;
  }

  to {
    -webkit-transform: translate(-40vw,100vw) scale(.1);
    transform: translate(-40vw,100vw) scale(.1);
    opacity: 0;
  }
}

.wrapper {
  width: 100%;
  color: #ddd;
  transition: color .1s;
  position: relative;
}

.in {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  display: flex;
  flex-direction: column;
}

.image {
  position: relative;
  flex-grow: 1;
  width: 100%;
  -webkit-transform: scale(.9);
  transform: scale(.9);
}

.imageFrame,
.imageIn {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.imageIn {
  -o-object-fit: cover;
  object-fit: cover;
  -o-object-position: center bottom;
  object-position: center bottom;
  -webkit-transform-origin: center 97%;
  transform-origin: center 97%;
  transition-property: opacity,-webkit-transform;
  transition-property: transform,opacity;
  transition-property: transform,opacity,-webkit-transform;
  transition-duration: .3s;
  opacity: 0;
}

.imageIn.viewFlying,
.isPlaceholderVisible .imageIn.viewPlaceholder,
.isRealVisible .imageIn.viewReal {
  opacity: 1;
}

.viewSmall .imageIn.viewPlaceholder {
  -webkit-transform: scale(.9,.72);
  transform: scale(.9,.72);
}

.imageIn.viewFlying {
  z-index: 1;
  -webkit-animation: a .5s ease-out forwards;
  animation: a .5s ease-out forwards;
}

.info {
  margin-top: 1.5em;
  padding: 2em 1em 1em;
  color: #000;
  flex-shrink: 0;
  min-height: 8em;
}

.info,
.infoIn {
  position: relative;
}

.infoIn {
  z-index: 1;
  display: flex;
  flex-wrap: wrap;
}

.viewShadow .name:before,
.viewShadow .note:before,
.viewShadow .price:before {
  content: "";
  height: .8em;
  background-color: currentColor;
  border-radius: .1em;
  display: inline-block;
  max-width: 100%;
}

.name,
.note,
.price {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

.name {
  margin: 0 .5em 0 0;
  font-weight: 600;
}

.name:before {
  width: 6em;
  opacity: .22;
}

.price {
  margin: 0;
  position: relative;
  top: .4em;
}

.price:before {
  width: 4em;
  opacity: .1;
}

.note {
  width: 100%;
  margin: .5em 0 0;
}

.note:before {
  width: 15em;
  opacity: .1;
}

.addToCart {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  z-index: 1;
}




