.wrapper {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 2;
  background-color: rgba(0,0,0,.2);
  padding: 1em;
  visibility: hidden;
  transition-property: opacity,visibility;
  transition-duration: .3s;
  display: flex;
  align-items: center;
  flex-direction: column;
  opacity: 0;
  overflow: auto;
}

.wrapper:after,
.wrapper:before {
  content: "";
  flex-grow: 1;
}

.wrapper:after {
  flex-grow: 2;
}

.wrapper.isActive {
  opacity: 1;
  visibility: visible;
}

.in {
  position: relative;
  z-index: 1;
  border-radius: .3em;
  background-color: #fff;
  padding: 1em;
  min-width: 16em;
  min-height: 16em;
  transition-property: opacity,-webkit-transform;
  transition-property: transform,opacity;
  transition-property: transform,opacity,-webkit-transform;
  transition-duration: .2s;
  transition-delay: .3s;
  transition-timing-function: cubic-bezier(.57,.36,.7,1.31);
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
  opacity: 0;
}

.isActive .in {
  -webkit-transform: none;
  transform: none;
  opacity: 1;
  transition-delay: 0s;
}

.close {
  position: absolute;
  top: -.1em;
  right: -.1em;
  border: none;
  background: none;
  padding: .5em;
  font-size: inherit;
  opacity: .2;
  transition: opacity .2s;
}

.close:focus,
.close:hover,
.content,
.head {
  opacity: 1;
}

.title {
  margin: 0 0 1em;
  font-size: 1.5em;
  text-align: center;
  font-weight: 600;
}

.backgroundClose {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  cursor: auto;
}




