.wrapper {
  position: relative;
  display: flex;
  margin: 0 auto;
  max-width: 40em;
}

.section {
  border: none;
  background: none;
  outline: none;
  font-size: 1.1em;
  color: #c5c5c5;
  padding: .5em 0;
  transition: color .3s;
  font-weight: 600;
}

.section.isActive {
  color: #000;
}

.bar,
.section {
  width: 33.33333%;
}

.bar {
  position: absolute;
  bottom: 0;
  left: 0;
  border-bottom: .1em solid;
  transition: -webkit-transform .3s;
  transition: transform .3s;
  transition: transform .3s,-webkit-transform .3s;
}

.section:first-child.isActive ~ .bar {
  -webkit-transform: translateX(0);
  transform: translateX(0);
}

.section:nth-child(2).isActive~.bar {
  -webkit-transform: translateX(100%);
  transform: translateX(100%);
}

.section:nth-child(3).isActive~.bar {
  -webkit-transform: translateX(200%);
  transform: translateX(200%);
}




