.wrapper {
  flex-grow: 1;
  font-size: 2em;
  text-align: center;
}

.item {
  display: flex;
  width: 100%;
}

.item:before {
  content: "";
}

.card {
  flex-grow: 1;
}

.icon {
  font-size: 3.4em;
  opacity: .8;
}

.remove {
  align-self: flex-end;
  text-align: right;
}

.removeButton {
  border: none;
  background: none;
  font-size: inherit;
  padding: 0;
  opacity: .5;
  transition: opacity .3s;
}

.removeButton:focus,
.removeButton:hover {
  opacity: 1;
}

.item:before,
.remove {
  min-width: 1em;
}




