@import '../../common';

.wrapper {
	position: absolute;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
	z-index: 2;
	background-color: #FFFFFF;
	padding: 1em;
	transform: translateY(100%);
	visibility: hidden;
	transition-property: transform, visibility;
	transition-duration: .3s;
	display: flex;
	flex-direction: column;
	pointer-events: auto;

	&.isActive {
		transform: none;
		visibility: visible; } }

.head {
	flex-shrink: 0; }

.content {
	flex-grow: 1;
	display: flex; }

.title {
	margin: 0 0 1em;
	font-size: 2em;
	text-align: center;
	font-weight: $fw-bold; }



