$product-info-top-space: 1.5em;
$product-small-scale: .8;
$product-info-min-height: 8em;

$bottom-bar-height: 4em;
$bottom-bar-transition-duration: .3s;

$brand-color: #118CFF;
$error-color: #E55565;
$warning-color: #F69D00;
$info-color: #007BFF;

$fw-light: 300;
$fw-bold: 600;

$basket-animation-duration: .5s;

@mixin horizontal-scroll {
	overflow: auto;
	overflow-scrolling: touch;
	-webkit-overflow-scrolling: touch;
	-webkit-mask-image: linear-gradient(90deg, rgba(#000000, 0), #000000 1em, #000000 calc(100% - 1em), rgba(#000000, 0)); // @TODO: remove prefix

	&::-webkit-scrollbar {
		width: 0;
		height: 0;
		background: transparent; } }



