.wrapper {
	flex-grow: 1;
	font-size: 2em;
	text-align: center; }

.item {
	display: flex;
	width: 100%;

	&::before {
		content: ''; } }

.card {
	flex-grow: 1; }

.icon {
	font-size: 3.4em;
	opacity: .8; }

.number {
 }	//

.remove {
	align-self: flex-end;
	text-align: right;

	&Button {
		border: none;
		background: none;
		font-size: inherit;
		padding: 0;
		opacity: .5;
		transition: opacity .3s;

		&:hover,
		&:focus {
			opacity: 1; } } }

.item::before,
.remove {
	min-width: 1em; }



