@import '../../common';

.wrapper {
	display: flex;
	flex-direction: column;
	flex-grow: 1;
	overflow: auto;
	padding: 2em 1em 1em;
	margin: 0 -1em; }

.list {
	$action-width: 2em;

	margin-bottom: 3em;

	&Item {
		position: relative;
		padding-right: $action-width;

		& + & {
			margin-top: 1.5em; } }

	&Title {
		font-size: 1.25em;
		font-weight: $fw-bold; }

	&Note {
		margin: .25em 0 0; }

	&Arrow {
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		width: $action-width;
		display: flex;
		justify-content: flex-end;
		align-items: center;
		opacity: .3; }

	&Action {
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 100%;
		opacity: 0; } }

.other {
	display: flex;

	&::before {
		content: '';
		flex-grow: 1; } }

.logout {
	border: none;
	outline: none;
	padding: 0;
	background: none;
	font-size: inherit;
	order: -1; }

.social {
	color: inherit;
	text-decoration: none;
	font-size: 2em;
	opacity: .9;

	& + & {
		margin-left: .5em; } }

.popups {
	position: absolute;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
	pointer-events: none;
	z-index: 2; }

.version {
	flex-grow: 1;
	display: flex;
	justify-content: flex-end;
	align-items: flex-end;
	padding-top: 1em; }



