@-webkit-keyframes a {
  0% {
    -webkit-transform: translateY(100%);
    transform: translateY(100%);
    opacity: 0;
  }

  10% {
    opacity: 1;
  }
}

@keyframes a {
  0% {
    -webkit-transform: translateY(100%);
    transform: translateY(100%);
    opacity: 0;
  }

  10% {
    opacity: 1;
  }
}

.wrapper {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  pointer-events: none;
  z-index: 1;
}

.item {
  width: 100%;
  display: flex;
  -webkit-animation: a .3s;
  animation: a .3s;
  pointer-events: auto;
  border-top: 1px solid rgba(0,0,0,.1);
}

.item.viewDanger {
  background-color: #e55565;
}

.item.viewWarning {
  background-color: #f69d00;
}

.item.viewInfo {
  background-color: #007bff;
  color: #fff;
}

.content {
  flex-grow: 1;
  padding: 1em;
}

.dismiss {
  padding: 1em;
  font-size: inherit;
  border: none;
  background: none;
  color: rgba(0,0,0,.4);
}




