.wrapper {
	display: flex;
	flex-direction: column;
	width: 100%; }

.products {
	flex-grow: 1;
	overflow: auto;
	margin: 0 -1em;
	padding: 0 1em; }

.deliveryDetails {
	flex-shrink: 0;
	margin: 0 -1em;
	padding: 1em 1em 1.5em;
	background-color: #F5F5F5;

	&Title {
		font-size: 1.25em;
		font-weight: 600;
		margin-bottom: .4em; }

	&Divider {
		margin: .75em (-.5em) .5em;
		height: 1px;
		background-color: #e9e9e9; } }

.order {
	&Price {
		&Value {
			font-weight: 700; } }

	&Action {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		opacity: 0; } }



