@import '../../common';

 :global {
	body,
	html {
		margin: 0;
		padding: 0; }

	body {
		font-family: 'Source Sans Pro', sans-serif;
		font-weight: $fw-light; }

	h1,
	h2,
	h3,
	h4,
	h5,
	input,
	textarea,
	button {
		font-family: inherit;
		font-weight: $fw-light; }

	*,
	*::before,
	*::after {
		box-sizing: border-box; }

	button {
		cursor: pointer;
		outline: none;
		-webkit-tap-highlight-color: rgba(0, 0, 0, 0);

		&[disabled] {
			cursor: not-allowed; } }

	label {
		cursor: pointer; } }

.wrapper {
	display: flex;
	position: fixed;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
	overflow: hidden;
	padding: 1em; }

.in {
	width: 100%; }



