:global body,
:global html {
  margin: 0;
  padding: 0;
}

:global body {
  font-family: Source Sans Pro,sans-serif;
  font-weight: 300;
}

:global button,
:global h1,
:global h2,
:global h3,
:global h4,
:global h5,
:global input,
:global textarea {
  font-family: inherit;
  font-weight: 300;
}

:global *,
:global :after,
:global :before {
  box-sizing: border-box;
}

:global button {
  cursor: pointer;
  outline: none;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
}

:global button[disabled] {
  cursor: not-allowed;
}

:global label {
  cursor: pointer;
}

.wrapper {
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  overflow: hidden;
  padding: 1em;
}

.in {
  width: 100%;
}




