.wrapper {
  margin: 0 -1em;
  flex-grow: 1;
  position: relative;
  --product-width: 100%;
}

@media (min-aspect-ratio:9/10) {
  .wrapper {
    --product-width: 80%;
  }
}

@media (min-aspect-ratio:1/1) {
  .wrapper {
    --product-width: 50%;
  }
}

@media (min-aspect-ratio:6/5) {
  .wrapper {
    --product-width: 33.33333%;
  }
}

@media (min-aspect-ratio:7/5) {
  .wrapper {
    --product-width: 25%;
  }
}

@media (min-aspect-ratio:9/5) {
  .wrapper {
    --product-width: 20%;
  }
}

@media (min-aspect-ratio:13/5) {
  .wrapper {
    --product-width: 14.28571%;
  }
}

@media (min-aspect-ratio:17/5) {
  .wrapper {
    --product-width: 11.11111%;
  }
}

@media (min-aspect-ratio:4/1) {
  .wrapper {
    --product-width: 9.09091%;
  }
}

@media (min-aspect-ratio:5/1) {
  .wrapper {
    --product-width: 7.69231%;
  }
}

.counter {
  overflow: auto;
  overflow-scrolling: touch;
  -webkit-overflow-scrolling: touch;
  -webkit-mask-image: linear-gradient(90deg,transparent,#000 1em,#000 calc(100% - 1em),transparent);
  padding-top: 1em;
  height: 100%;
}

.counter::-webkit-scrollbar {
  width: 0;
  height: 0;
  background: transparent;
}

.counterIn {
  display: flex;
  height: inherit;
}

.counterIn:after,
.counterIn:before {
  content: "";
  width: calc((100% - var(--product-width)) / 2);
  display: block;
  flex-shrink: 0;
}

.product {
  width: var(--product-width);
  flex-shrink: 0;
  display: flex;
}

.background {
  bottom: -4em;
  height: 12em;
  background-color: #f6f6f6;
}

.background,
.labels {
  position: absolute;
  left: 0;
  right: 0;
}

.labels {
  bottom: 6em;
}




