@-webkit-keyframes a {
  0% {
    -webkit-transform: translateY(-100%);
    transform: translateY(-100%);
    opacity: 0;
  }
}

@keyframes a {
  0% {
    -webkit-transform: translateY(-100%);
    transform: translateY(-100%);
    opacity: 0;
  }
}

@-webkit-keyframes b {
  20% {
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
  }

  to {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}

@keyframes b {
  20% {
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
  }

  to {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}

@-webkit-keyframes c {
  19% {
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
  }

  to {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}

@keyframes c {
  19% {
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
  }

  to {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}

.items {
  display: flex;
}

.continue i {
  margin-left: .5em;
}

.action {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: none;
  background: none;
  outline: none;
}

.item {
  position: relative;
  height: 2em;
  -webkit-animation: a .3s .3s backwards;
  animation: a .3s .3s backwards;
}

.quantity {
  position: absolute;
  top: -.5em;
  right: -.4em;
  z-index: 1;
  font-size: .8em;
  min-width: 1.5em;
  text-align: center;
  color: #000;
}

.viewSmall .quantity {
  right: -.25em;
}

.image {
  position: relative;
  height: inherit;
  width: 2em;
  -webkit-transform-origin: center bottom;
  transform-origin: center bottom;
  -webkit-animation: b .3s .3s;
  animation: b .3s .3s;
}

.isAlt .image {
  -webkit-animation-name: c;
  animation-name: c;
}

.viewSmall .image {
  -webkit-transform: scale(.8);
  transform: scale(.8);
}

.imageIn {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}




